.button {
  background: none;
  border: 1px solid #FF1041;
  padding: 8px 20px;
  min-width: 220px;
  color: #FF1041;
  font-size: 15px;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
  border-radius: 70px;
  cursor: pointer; }
  .button:hover {
    background-color: #FF1041;
    color: #ffffff; }
  @media screen and (max-width: 480px) {
    .button {
      width: 100%; } }

.white {
  border-color: #ffffff;
  color: #ffffff;
  font-size: 18px; }
  .white:hover {
    background-color: #fff;
    color: #323232; }

.plain {
  border: none;
  background: none;
  font-size: 14px;
  color: #a8a8a8;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
  cursor: pointer;
  font-family: "OpenSansBold", sans-serif; }
  @media screen and (max-width: 480px) {
    .plain {
      margin-top: 15px; } }
  .plain:hover {
    color: #323232; }

.offset {
  margin-left: 20px; }

.full {
  width: 100%;
  min-width: 0; }



