@font-face {
  font-family: "OpenSansRegular";
  font-style: normal;
  font-weight: normal;
  src: url("./assets/fonts/OpenSansRegular/OpenSansRegular.eot?#iefix") format("embedded-opentype"), url("./assets/fonts/OpenSansRegular/OpenSansRegular.woff") format("woff"), url("./assets/fonts/OpenSansRegular/OpenSansRegular.ttf") format("truetype"); }

@font-face {
  font-family: "OpenSansLight";
  font-style: normal;
  font-weight: normal;
  src: url("./assets/fonts/OpenSansLight/OpenSansLight.eot?#iefix") format("embedded-opentype"), url("./assets/fonts/OpenSansLight/OpenSansLight.woff") format("woff"), url("./assets/fonts/OpenSansLight/OpenSansLight.ttf") format("truetype"); }

@font-face {
  font-family: "OpenSansBold";
  font-style: normal;
  font-weight: normal;
  src: url("./assets/fonts/OpenSansBold/OpenSansBold.eot?#iefix") format("embedded-opentype"), url("./assets/fonts/OpenSansBold/OpenSansBold.woff") format("woff"), url("./assets/fonts/OpenSansBold/OpenSansBold.ttf") format("truetype"); }

html {
  font-size: 16px; }

body {
  -webkit-font-smoothing: antialiased;
  line-height: 1.42; }

html, body {
  width: 100%; }

.wrapper {
  min-height: 100%;
  overflow: hidden;
  position: relative; }

.wrapper:after {
  content: "";
  display: block; }

footer {
  position: relative; }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

img {
  max-width: 100%; }

body {
  font-family: "OpenSansRegular", sans-serif;
  font-size: 16px;
  color: #323232; }

button {
  background: none;
  border: none; }

button:focus {
  outline: none;
  background: none; }



