.container {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #ffffff; }

.avatar {
  width: 120px;
  height: 120px;
  margin: 0 auto 30px;
  -webkit-animation: showAvatar .8s forwards;
          animation: showAvatar .8s forwards; }
  .avatar img {
