.container {
  position: relative;
  overflow: hidden;
  display: block;
  height: 70vh;
  border-radius: 4px; }
  @media screen and (max-width: 768px) {
    .container {
      height: 40vh; } }
  .container:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left bottom, left top, from(#323232), color-stop(70%, transparent));
    background: -webkit-linear-gradient(bottom, #323232 0%, transparent 70%);
    background: -o-linear-gradient(bottom, #323232 0%, transparent 70%);
    background: linear-gradient(to top, #323232 0%, transparent 70%);
    z-index: 5; }
  .container:hover .more {
    top: 0; }

.more {
  width: 100%;
  z-index: 8;
  padding: 10px;
  position: absolute;
  background: #323232;
  top: -100px;
  left: 0;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
  color: #ffffff;
  font-family: "OpenSansLight", sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  border-radius: 4px 4px 0 0; }

.moreIcon {
  fill: #ffff;
  width: 20px;
  height: 20px;
  margin-right: 10px; }

.image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  border-radius: 4px; }

.caption {
  position: absolute;
  left: 20px;
  bottom: 20px;
  z-index: 10; }

.techList {
  margin-top: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.techIcon {
  color: #ffffff;
  font-size: 50px; }



