.container {
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease; }

.controls {
  margin-top: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center; }

.add {
  cursor: pointer;
  background: none;
  border: none; }
  .add:hover .icon {
    fill: #701FDB; }

.close {
  cursor: pointer;
  margin-left: 15px; }
  .close .icon {
    fill: #a8a8a8;
    width: 15px;
    height: 15px; }
  .close:hover .icon {
    fill: #323232; }

.icon {
  width: 24px;
  height: 24px;
  fill: #323232;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease; }

.addEnter {
  opacity: 0; }

.addEnterActive {
  opacity: 1; }

.addExit {
  opacity: 1; }

.addExitActive {
  opacity: 0; }



