.container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.form {
  display: block;
  width: 350px;
  margin: 0 auto; }
  @media screen and (max-width: 480px) {
    .form {
      width: 100%; } }

.link {
  margin-bottom: 20px;
  display: block; }

.controls {
  text-align: center; }

.loader {
  margin-top: 20px;
  position: relative; }



