.container {
  border-radius: 4px;
  -webkit-box-shadow: 0 3px 6px 2px rgba(50, 50, 50, 0.3);
          box-shadow: 0 3px 6px 2px rgba(50, 50, 50, 0.3);
  padding: 20px;
  margin-bottom: 50px; }
  @media screen and (max-width: 480px) {
    .container {
      padding: 10px; } }

.animated {
  -webkit-animation: showPanel .8s forwards;
          animation: showPanel .8s forwards; }

@-webkit-keyframes showPanel {
  from {
    -webkit-transform: translateX(-1000px);
            transform: translateX(-1000px); }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes showPanel {
  from {
    -webkit-transform: translateX(-1000px);
            transform: translateX(-1000px); }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }



